<template>
  <div class="achievements">
    <h2>个人绩效</h2>
    <div class="table_box">
      <table class="table">
        <thead>
          <tr>
            <th>姓名</th>
            <th>本月绩效比例</th>
            <th>绩效评定时间</th>
            <th>评价</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in personnelAch" :key="index">
            <td v-if="index == 0">{{ item.personnel_name }}</td>
            <td v-else></td>
            <td>{{ item.proportion }}</td>
            <td>{{ item.evaluate_date }}</td>
            <td>{{ item.evaluate }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div style="padding-top:48px">
      <span>前往</span>
      <router-link to="/achievements_eval">员工绩效评定</router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  data() {
    return {
      // 个人绩效数据列表
      personnelAch: []
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 查看首页绩效列表
    visitAch() {
      this.$api2.visitAch().then(res => {
        console.log(res);
        this.personnelAch = res.data.data;
      });
    }
  },
  created() {
    console.log(this.$api2);
    this.visitAch();
  },
  mounted() {}
};
</script>
<style scoped>
/* @import url("/static/css/thead_fixed_table.css"); */
@import "../../../static/css/thead_fixed_table.css";

/* .table{
    display: block;
    width: 612px;
    margin: 0 auto;
} */
.table_box {
  width: 34vw;
  text-align: center;
}
h2 {
  padding: 40px;
}
.table {
  width: 31vw;
  margin: auto;
}
.achievements {
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding-bottom: 20px;
  width: 60%;
  margin: 40px auto;
  border-radius: 5px;
}
</style>



